body
{
	background:#F7F9F9
}

code
{
	font-family:source-code-pro,Menlo,Monaco,Consolas,'Courier New',monospace
}

main
{
	flex:1
}

article
{
	font-family:Montserrat;
	font-style:normal;
	font-size:initial
}

.site-wrapper
{
	display:flex;
	flex-direction:column;
	align-items:stretch;
	min-height:100vh;
	background:#F7F9F9
}

.container-1
{
	display:flex;
	flex-direction:column;
	align-items:center;
	width:400px;
	height:500px;
	background:#FDFEFE;
	margin:auto
}

.container-2
{
	height:100px;
	display:flex;
	flex-direction:row;
	align-items:center;
	justify-content:space-between;
	background:#FDFEFE
}

.container-3
{
	display:flex;
	flex-direction:row;
	align-items:center;
	justify-content:space-between;
	background:inherit
}

.container-4
{
	display:flex;
	flex-direction:column;
	justify-content:space-evenly;
	align-items:center;
	width:804px;
	height:320px;
	background:#FDFEFE;
	box-shadow:0 4px 4px rgba(0,0,0,0.25);
	border-style:dashed;
	border-color:#E5E5E5
}

.container-5
{
